import React from 'react'
import { Button, Typography } from '@material-ui/core'

import '../style/404.scss'

const PageNotFound = ({ history }) => {
  const handleMoveHomePage = () => {
    history.replace('/')
  }

  const handleMovePrevPage = () => {
    history.goBack()
  }

  return (
    <div className="page-error">
      <Typography variant="h3" gutterBottom>  </Typography>

      <div className="buttons">
        <Button onClick={handleMoveHomePage} color="primary"> </Button>
        <Button onClick={handleMovePrevPage} color="secondary"> </Button>
      </div>
    </div>
  )
}

export default PageNotFound