import React, { useState, useEffect, useMemo } from 'react'
import { Paper, Box, Tooltip, TextField, MenuItem } from '@material-ui/core'
import MaterialTable, { MTableToolbar } from 'material-table'
import StartEndDatePicker from '../datePicker/StartEndDatePicker'
import DatePicker from '../datePicker/DatePicker'
import moment from 'moment'

import clsx from 'clsx'
import produce from 'immer'

import './table.scss'


const Table = ({ term = false, termStart = '', termColumn = '', start = '', end = '', scrollable = false, useTermDiffValidate = false, components = {}, label = '', options = {}, localization = {}, isAddToolbar = false, AddToolbar = {}, AddToolbarProps = {}, data = [], ...props }) => {
  const pageSizeOptions = options.pageSizeOptions || [100, 50, 20]

  const [pageSize, setPageSize] = useState(options.pageSize || 100)
  const [searched, setSearched] = useState([])
  const [filter, setFilter] = useState({
    start: new Date(termStart || '2019-01-01'),
    end: new Date()
  })

  const handleChangeDate = (start, end) => {
    if (useTermDiffValidate && moment(end).diff(moment(start), 'months', true) > 3) {
      alert('3.')

      setFilter({
        start: new Date((new moment(end)).subtract(3, 'months').format('YYYY-MM-DD')),
        end
      })

      return false
    } 

    if (props.setDate) {
      props.setDate({ start, end })
    }

    setFilter({ start, end })
  }

  useEffect(() => {
    Array.isArray(data) && (
      setSearched(data.filter(item => (
        (!term || !filter.start || filter.start <= new Date(item[termColumn]))
        && (!term || !filter.end || filter.end >= new Date(item[termColumn]))
      )))
    )
  }, [data, filter.start, filter.end])


  return (
    <div style={{position: 'relative'}}>
      {term && (
        <div style={{
          position: 'absolute',
          right: '30px',
          top: '20px',
          zIndex: 999
        }}>
          <StartEndDatePicker
            start={filter.start}
            end={filter.end}
            onChange={handleChangeDate}
          />
        </div>
      )}
      <MaterialTable
        key={pageSize}
        data={searched}
        components={{
          Container: props => (
            <Paper {...props} className={clsx('custom_table', scrollable && 'scrollable')} />
          ),
          Toolbar: props => (
            <div className="toolbar">
              <Box display="flex" alignItems="center">
                <MTableToolbar {...props} />
                
                {isAddToolbar && (
                  <AddToolbar {...AddToolbarProps}></AddToolbar>
                )}

                <TextField
                  select
                  value={pageSize}
                  onChange={e => setPageSize(e.target.value)}
                  className="customPageSizeInput"
                >
                  {pageSizeOptions.map((size, k) => (
                    <MenuItem value={size} key={k}>{size} </MenuItem>
                  ))}
                </TextField>
              </Box>
            </div>
          ),
          ...components
        }}
        localization={{
          body: {
            emptyDataSourceMessage: '  .',
            filterRow: {
              filterTooltip: ''
            },
            editRow: {
              saveTooltip: '',
              cancelTooltip: '',
              deleteText: '   ?'
            },
            addTooltip: '',
            deleteTooltip: '',
            editTooltip: ''
          },
          grouping: {
            placeholder: '   '
          },
          toolbar: {
            searchTooltip: '',
            searchPlaceholder: '',
            exportTitle: ' ',
            exportName: 'CSV ',
            showColumnsTitle: '',
            addRemoveColumns: '   '
          },
          pagination: {
            labelRowsSelect: `${label}`,
            firstTooltip: ' ',
            previousTooltip: ' ',
            nextTooltip: ' ',
            lastTooltip: ' ',
            labelDisplayedRows: `{count}${label}  {from}~{to}${label}`
          },
          ...localization
        }}
        options={{
          draggable: false,
          search: false,
          doubleHorizontalScroll: true,
          columnsButton: true,
          ...options,
          pageSize,
          pageSizeOptions
        }}
        {...props}
      />
    </div>
  )
}

export default Table