import React, { useState, useEffect } from 'react'
import { format } from 'date-fns'
import { Paper, Box, TextField, MenuItem } from '@material-ui/core'

import API from '../api/Api'

import { NavLink, withRouter } from 'react-router-dom'
import DefaultTemplate from '../components/template/DefaultTemplate'
import Table from '../components/table/Table'
import MaterialTable, { MTableToolbar } from 'material-table'


const OrgAccount = ({ history }) => {
  const [pageSize, setPageSize] = useState(100)

  const columns = [
    {
      field: 'name',
      title: ''
    },
    {
      field: 'hp',
      title: ''
    },
    {
      field: 'p_code',
      title: 'pcode'
    },
    {
      field: 'entry_dt',
      title: ''
    },
    // {
    //   field: 'idk',
    //   title: ''
    // },
    // {
    //   field: 'disp_class',
    //   title: '  '
    // }
  ]

  const init = async (page = 0, pageSize = 100, keywords = []) => {
    keywords = keywords.length ? keywords : ''
    const { totalCount = 0, items = [] } = await API.getAccounts('member', { page, pageSize, keywords })

    const data = items.map(item => {
      item.entry_dt = format(new Date(item.entry_dt), 'yyyy-MM-dd HH:mm:dd')
      return item
    })
    
    return {
      data,
      page,
      totalCount
    }
  }

  useEffect(() => {
    init()
  }, [])
  
  const handleRowClick = (e, rowData) => {
    history.push(`/member/account/view/${rowData.uuid}`)
    
    return false
  }

  return (
    <>
      <DefaultTemplate>
        <MaterialTable
          title="  "
          columns={columns}
          data={query =>
            new Promise((res, rej) => {
              let keywords = query.filters.map(v => ({
                field: v.column.field,
                keyword: v.column.tableData.filterValue
              }))

              init(query.page, query.pageSize, keywords).then(result => {
                res(result)
              })
            })
          }
          onRowClick={handleRowClick}
          options={{
            draggable: false,
            search: false,
            doubleHorizontalScroll: true,
            columnsButton: true,
            filtering: true,
            exportButton: true,
            pageSize,
            pageSizeOptions: [100, 50, 20],
          }}
          components={{
            Container: props => (
              <Paper {...props} className="custom_table" />
            ),
            Toolbar: props => (
              <div className="toolbar">
                <Box display="flex" alignItems="center">
                  <MTableToolbar {...props} />

                  <TextField
                    select
                    value={pageSize}
                    onChange={e => setPageSize(e.target.value)}
                    className="customPageSizeInput"
                  >
                    {[100, 50, 20].map((size, k) => (
                      <MenuItem value={size} key={k}>{size} </MenuItem>
                    ))}
                  </TextField>
                </Box>
              </div>
            ),
          }}
          localization={{
            body: {
              emptyDataSourceMessage: '  .',
              filterRow: {
                filterTooltip: ''
              },
              editRow: {
                saveTooltip: '',
                cancelTooltip: '',
                deleteText: '   ?'
              },
              addTooltip: '',
              deleteTooltip: '',
              editTooltip: ''
            },
            grouping: {
              placeholder: '   '
            },
            toolbar: {
              searchTooltip: '',
              searchPlaceholder: '',
              exportTitle: ' ',
              exportName: 'CSV ',
              showColumnsTitle: '',
              addRemoveColumns: '   '
            },
            pagination: {
              labelRowsSelect: ``,
              firstTooltip: ' ',
              previousTooltip: ' ',
              nextTooltip: ' ',
              lastTooltip: ' ',
              labelDisplayedRows: `{count}  {from}~{to}`
            }
          }}
        />
      </DefaultTemplate>
    </>
  )
}

export default withRouter(OrgAccount)