import React, { useState, useEffect } from 'react'

import API from '../../api/Api'
import useInputs from '../../hooks/useInputs'

import Dialog from './Dialog.js'
import { Grid, IconButton, TextField } from '@material-ui/core'
import { Add } from '@material-ui/icons'
import Input from '../input/Input'

const SystemAccountDialog = ({ init }) => {
  const [state, onChange] = useInputs({
    user: '',
    password: '',
    manager: '',
    contact: '',
    email: '',
    authority: '',
  })
  const [open, setOpen] = useState(false)

  const handleOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleAdd = async () => {
    const valid = Object.keys(state).every(key => state[key].trim().length > 0)

    if (!valid) {
      return alert(' ')
    }

    const { data: isExist } = await API.existUser(state.user)

    if (isExist) {
      alert('    ')
      return
    }

    const res = await API.addAccount('system', state)

    if (res.status === 200) {
      alert('  .')

      init()
      handleClose()
    } else {
      alert('   .')
    }

  }


  const systemAccountAddForm = [
    { onChange: onChange, value: state.user, name: 'user', label: "" },
    { onChange: onChange, value: state.password, name: 'password', label: "", type: 'password' },
    { onChange: onChange, value: state.manager, name: 'manager', label: " " },
    { onChange: onChange, value: state.contact, name: 'contact', label: " " },
    { onChange: onChange, value: state.email, name: 'email', label: " " },
    { onChange: onChange, value: state.authority, name: 'authority', label: "" },
  ]

  return (
    <>
      <IconButton onClick={handleOpen}>
        <Add></Add>
      </IconButton>
      <Dialog
        className="systemAccountDialog"
        open={open}
        onClose={handleClose}
        message={{
          title: '  '
        }}
        buttons={[{ onClick: handleAdd, color: 'primary', text: '' }]}
      >
        <form noValidate autoComplete="off">
          <Grid container spacing={2}>
            {systemAccountAddForm.map((input, index) => (
              <Grid item xs={6}>
                <Input variant="outlined" key={index} {...input} />
              </Grid>
            ))}
          </Grid>
        </form>
      </Dialog>
    </>
  )
}

export default SystemAccountDialog
